<template>
  <div class="page-container">
    <Loader v-if="loader" />
    <div class="wrap-1500">
      <div class="wrap-1300">
        <div class="step-box mb-5">
          <ul class="reset">
            <li class="current">
              <i></i>
              <p>購物清單</p>
            </li>
            <li class="current">
              <i></i>
              <p>填寫資料</p>
            </li>
            <li class="current">
              <i></i>
              <p>訂單完成</p>
            </li>
          </ul>
        </div>
        <div class="finish">
          <div class="txt">
            <div class="ti-box text-center">
              <div class="ti">購買完成</div>
            </div>
            <div class="icon"><i class="bi bi-check-circle"></i></div>
            <p class="text-center">已傳送課程連結至您的信箱，若有任何問題歡迎聯絡我們。</p>
          </div>
          <div class="list">
            <div class="head">訂單明細</div>
            <div class="detail">
              <p v-for="(product, i) in orders.products" :key="i"><i>課程名稱</i><span>{{ product.product.title }}</span></p>
            </div>
          </div>
          <div class="list">
            <div class="head">訂購人資料</div>
            <div class="detail">
              <div v-if="orders.user">
                <p><i>姓名</i><span>{{ orders.user.name }}</span></p>
                <p><i>電話</i><span>{{ orders.user.tel }}</span></p>
                <p><i>電子郵件</i><span>{{ orders.user.email }}</span></p>
                <p><i>地址</i><span>{{ orders.user.address }}</span></p>
                
              </div>
            </div>
          </div>
        </div>
        <div class="btn-wrap d-flex justify-content-center mt-5"><router-link to="/index" class="btn-style" title="返回首頁"><span>返回首頁</span><i class="bi bi-arrow-right"></i></router-link></div>
      </div>
    </div>
  </div>
</template>

<script>
  import Loader from '@/components/Loader.vue';
  export default {
    components:{
      Loader,
    },
    data() {
      return {
        loader: false,
        orders: [],
        orderId: null,
      };
    },
    methods: {
      getOrder() {
        this.loader = true;
        const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order/${this.orderId}`;
        this.$http.get(api)
        .then((res) => {
          this.orders = res.data.order;
          this.loader = false;
        })
      },
    },
    created() {
      this.orderId = this.$route.query.id;
      this.getOrder();
    },
  }
</script>

